<template>
	<view>
		这是text组件
		这是父组件传递过来的值{{title}}
		<button type="default" @click="sendNum">给父组件传值</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				num : 100
			};
		},
		props:[
			"title"
		],
		methods:{
			sendNum(){
				this.$emit('myEven',this.num);
			}
		}
	}
</script>

